import * as Yup from 'yup'

const validate=Yup.object().shape({
    nm:Yup.string().required('username cannot be empty').min(5,'minimun 5 characters needed').max(10,'max 10 char allowed'),
    age:Yup.number().required('age can not be empty').positive('age must be positive').max(20,'age above 20 not allowed'),
dept:Yup.string().required('dept can not be empty').length(10,'dept should be of 10 characters').lowercase().strict()
})


export default validate;