import React,{useState,useEffect} from 'react'
import axios from 'axios'

const page_size = 10;

function Apip() {

    const [info,setInfo] = useState([])
    const [page ,setPage] = useState(0)

   useEffect(()=>{
        axios.get('https://dummyjson.com/products?limit=500')
        .then((res)=>{
            setInfo(res.data.products)
    
        }).catch((err)=>{
            console.log(err)
    
        })

   },[])

   const totalProduct = info.length;
   const noOfPage = Math.ceil(totalProduct/page_size);
   const start = page * page_size;
   const end = start + page_size;

   const handleclick=(n)=>{
    setPage(n)


   }
   const prev=()=>{
    setPage((prev)=>prev-1)

   }

   const next=()=>{
    setPage((prev=>prev+1))

   }



  return (
    <>
    <div className='container text-center'>
    <h3><span className='bg-dark text-light fs-1'>SUFIAN</span><span className='bg-warning fs-1'>HUB</span></h3>
    </div>
    
    
       <div className='container d-flex flex-wrap border bg-dark text-light text-center ' >
       {info.slice(start,end).map((v)=>{
            return <ul>
                <img src={v.thumbnail} alt={v.title} className='w-50px border text-center px-10 ms-5 mt-5'/>
                <li>{v.title}</li>
            </ul>
        })}
      
       </div>
       <div className='container py-3 text-center'>
       <button disabled = {page===0} onClick={prev}>◀️</button>
            {
                [...Array(noOfPage).keys()].map((n)=>{
                    return <button className={`px-3 ms-1 ${page === n ? 'bg-warning' : ''}`} onClick={()=>{handleclick(n)}} >{n}</button>

                })
            }
            <button disabled = {page === noOfPage-1} className='ms-1' onClick={next}>▶️</button>
        </div>
    
    </>
  )
}

export default Apip
